@import url('https://fonts.googleapis.com/css2?family=Alegreya:ital,wght@0,400..900;1,400..900&display=swap');
@import "../fonts/fonts.css";

.alegreya {
  font-family:monospace;
  font-optical-sizing: auto;
  font-style: normal;
}
body{
  font-family: 'Merienda';
  background-image: linear-gradient(to bottom right, #E0FBFC, #5C6B73);
  background-size: 100vw 100vh;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  overflow-x: hidden;
}
#home-body{
  font-size: 1.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100vh;
  width: 100vw;
}
#welcome-text{
  color: #253237;
  margin: auto;
  margin-left: 3rem;
  width: 100%;
  display: inline-block;
  z-index: 2;
}
#side-img {
  background-image: url('/src/imgs/tree2v1.1.png');
  background-size: cover; 
  background-position: 50%; 
  background-repeat: no-repeat; 
  width: 100%; 
  height: 100%; 
  display: block; 
  transform: scaleX(-1);
}
#img1{
  width: 100%;
  width:100%;
  margin: auto;
}
.company-text {
  position: fixed;
  left: 0;
  Top: 0;
  padding: 10px;
  font-size: 20px;
  color: #000;
}
@media screen and (min-width: 1000px) {
  .intro-text {
    max-width: 550px;
  }
}